//reset styles (smartgrid mixins)
@include reset();

//genereal grid styles (smartgrid mixins)
.container {
	@include wrapper();
	@include lg(max-width, 932px); //if you need fix container
	@include md(max-width, 708px); //if you need fix container
	@include sm(max-width, 506px); //if you need fix container
	@include xs(max-width, 290px); //if you need fix container

 }	//outline: 1px solid yellow

//custom styles
html {
	font-size: 10px; }
a {
	text-decoration: none; }
body {
	@include font($f-main, 400, 16px, 0.05em);
	background-color: #fdf9f4;
	position: relative; }

h2 {
	@include font($f-title, 800, 35px, normal); }

.button {
	background-color: #f2bd95;
	color: #2b221e;
	@include font($f-main, 800, 15px, 0.2em);
	text-decoration: none;
	text-transform: uppercase;
	height: 57px;
	line-height: 64px;
	display: block;
	max-width: 195px;
	text-align: center;
	border-radius: 30px;
	border: 2px solid transparent;
	transition: all 0.2s ease;

	@include xs-block() {
		font-size: 12px; }



	&:hover {
		border: 2px solid #f2bd95;
		background-color: transparent;
		color: #f2bd95; } }

.header {
	color: #fff;
	background: url('../img/header-bg.jpg') no-repeat right bottom /cover;
	padding-bottom: 19rem;

	@include sm-block() {
		padding-bottom: 11rem; }
	@include xs-block() {
		padding-bottom: 16rem; }
	h1 {
		@include font($f-title, 700, 53px, normal);
		line-height: 1.07;
		max-width: 585px;
		padding-top: 30rem;
		@include sm-block() {
			text-align: center;
			padding-top: 20rem; }
		@include xs-block() {
			@include font($f-title, 700, 37px, normal);
			padding-top: 16rem;
			max-width: 280px;
			margin: 0px auto; } }
	p {
		@include font($f-main, 400, 16px, 0.1em);
		max-width: 570px;
		line-height: 1.375;
		padding-top: 20px;
		@include sm(text-align, center); }
	a {
		margin-top: 46px;
		@include sm(margin, 20px auto);
		@include xs-block() {
			margin: 20px 0px 0px 0px;
			max-width: 320px; } } }

.header-fixed {
	//display: none
	position: fixed;
	width: 100%;
	top: 0;
	transition: all 0.2s ease;
	z-index: 100;

	a {
		color: #fff; }

	.header-fixed_wrapper {
		display: flex;
		justify-content: space-between;

		img {
			padding: 17px;
			height: 100%;


			@include lg(padding, 14px);
			@include xs-block() {
				width: 72px;
				padding: 10px; } }

		.header-fixed_companyName_less-md {
			display: none;
			@include font($f-title, 700, 18px, normal);
			color: #fff;
			padding-left: 20px;
			padding-top: 55px;

			span {
				@include xs-block() {
					font-size: 13px; } }

			@include md-block() {
				display: block; }

			@include xs-block() {
				padding-top: 22px;
				padding-left: 0;
				font-size: 14px; } }



		.header-contacts {
			display: flex;
			justify-content: flex-end;
			color: #fdf4ed;
			margin-top: 35px;
			margin-bottom: 20px;
			margin-right: 15px;

			@include lg-block() {
				margin-top: 35px;
				margin-bottom: 30px; }

			@include md(display, none);

			.header-contacts_item {
				@include font($f-main, 400, 16px, 0.05em);
				line-height: 30px;
				padding-left: 42px;
				margin-left: 50px;
				background: url('../img/header-cont-icon-1.png') no-repeat left center;

				@include lg(font-size, 13px);
				@include lg(margin-left, 30px); }

			.header-contacts_item__right {
				background: url('../img/header-cont-icon-2.png') no-repeat left center; }

			.header-contacts_item__name {
				background: none;
				font-size: 18px;
				margin-left: 0;
				padding-left: 0;

				@include lg-block {
					margin-left: 30px;
					margin-left: 0; }

				span {
					font-size: 14px; } } }

		ul.openedmenu {
			display: block; }
		ul {
			display: flex;

			@include md-block() {
				display: none;
				position: absolute;
				left: 0;
				top: 154px;
				width: 100%;
				text-align: center;
				background-color: #473f39;
				height: 100vh; }
			@include md-block() {
				top: 153px; }
			@include xs-block() {
				top: 93px; }


			li {
				@include md-block() {
					margin-bottom: 30px; }

				@include xs-block() {
					margin-top: 30px;
					margin-bottom: 10px; } }



			li a {
				padding: 11px 15px;
				margin-left: 8px;
				@include font($f-main, 500, 13px, 0.15em);
				text-transform: uppercase;
				border: 2px solid transparent;
				border-radius: 18px;
				transition: all 0.2s ease;

				@include lg-block() {
					padding: 7px 7px;
					font-size: 11px; }

				@include md-block() {
					//display: block
					margin-bottom: 25px;
					padding: 12px 12px;
					font-size: 18px;
					color: rgba(#fff, 0.4); }

				&:hover {
					border: 2px solid #f2bd95; } } } } }

.header-fixed.scrolled {
	background-color: rgba(#000, 0.8);
