	height: 74px;
	background-color: red;
	right: 50px;
	bottom: 50px;
	cursor: pointer;
	z-index: 100;
	display: none;
	@include md-block() {
		right: 20px;
		bottom: 20px; } }

.arrow-nav.arrow-down-scroll {
	background: url('../img/arrow-down-scroll.png') no-repeat center center; }

.arrow-nav.arrow-up-scroll {
	background: url('../img/arrow-up-scroll.png') no-repeat center center; }

.popup-overlay {
	z-index: 101;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	background-color: rgba(#000, .8);

	.popup-window {
		max-width: 375px;
		min-height: 450px;
		background: url('../img/popup-bg.png') no-repeat center center /cover;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		color: #fff;
		padding: 45px 30px;
		box-sizing: border-box;
		border-radius: 5px;
		min-height: 480px;

		img {
			display: block;
			margin: 0 auto; }
		a {
			position: absolute;
			top: 15px;
			right: 15px; }

		h2 {
			@include font($f-title, 700, 25px, normal);
			text-align: center;
			padding-bottom: 10px; }

		p {
			text-align: center;
			@include font($f-main, 300, 14px, 0.05em);
			margin-bottom: 20px; }

		input {
			@include input-text(90%, 45px);
			margin-bottom: 20px;
			margin: 0 auto 20px auto;
			padding-top: 16px; }

		button {
			@include input-text(90%, 45px);
			@include font($f-main, 800, 14px, 0.15em);
			margin: 0 auto;
			margin-bottom: 20px;
			text-transform: uppercase;
			background-color: #f2bd95;
			line-height: 27px;
			cursor: pointer;
			transition: all 0.2s ease;
			border-color: transparent;

			&:hover {
				background-color: darken(#f2bd95, 10%);
				border-color: transparent; } } } }

.popup-overlay.thanx-popup {
	display: none;

	.popup-window {
		padding-top: 70px;

		img {
			padding-bottom: 30px; } } }

.popup-overlay.order-popup {
	display: none; }

#loader {
    position: fixed;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    background: rgba(240, 240, 240, 0.91);
    overflow: hidden;
    display: none; }

#loader img {
    position: absolute;
    width: 50px;
    height: 50px;
    left: 50%;
    margin-left: -25px;
    top: 50%;
    margin-top: -25px; }

