.popup-overlay .popup-window label.error {
	text-align: center;
	margin-left: auto;
	margin-right: auto;
	display: block!important;
	width: 100%;
	position: absolute;
	margin-top: -76px;
	margin-left: -30px;
	font-size: 12px; }

