@mixin font($family, $weight, $size, $let-spacing) {
	font-family: $family;
	font-weight: $weight;
	font-size: $size;
	letter-spacing: $let-spacing; }

@mixin input-text($width, $height) {
	width: $width;
	height: $height;
	line-height: $height;
	box-sizing: border-box;
	display: block;
	border-radius: 25px;
	box-shadow: none;
	outline: none;
	border: 1px solid #e9e9e9;
	padding: 10px;
	font-size: 16px;
	background-color: #fbfbfb;
	padding-left: 25px;
	padding-right: 25px;

	&:hover, &:active, &:focus {
		border: 1px solid #f8dfca;
		background-color: #fff; } }

